import {ethers}  from 'ethers';
import { useEffect, useRef, useState } from 'react';
import { ChoiceSelect } from '../new_dir/first';
import { cc, pp } from './hey7';
import { abi, contractAddress, getStringOut, getStringOut2, urlForRecord } from './Var';
import CryptoJS from 'crypto-js'
import axios from 'axios';

function AdminPanel(){
    

    const [admin, setAdmin] = useState(false)

    async function loaded(){
    
        const ethereum = checkWeb3()
    
            try {
                await ethereum.request({ method: 'eth_requestAccounts' });
                const accounts = await ethereum.request({method: 'eth_accounts'});
                const provider = new ethers.providers.Web3Provider(ethereum)
                const network = await provider.getNetwork()
                a('Network name: '+network.name+" and chainId: "+network.chainId)
        
                if(accounts.length !== 0){
                // @ts-ignore
                const account1 = accounts[0];

                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner()
                const contract = new ethers.Contract(contractAddress, abi, signer)
    
                const data = await contract.admin()
                //a(`Data are2: ${data}`)
                //a(`Data are3: ${account1}`)

                if(data.toString().toLowerCase() == account1.toString().toLowerCase()){
                    setAdmin(true)
                }else setAdmin(false)
                }else setAdmin(false)
        
                }catch (err) {
    
                        let error1 = JSON.stringify(err)
                        //let error2 = JSON.stringify(err.reason)
                        
                        a(`Error2: ${error1}`)
                        setAdmin(false)

                        //setReportOnce(getStringOut(error2))
                        //setSlider(false)                    
                }
    }

    function checkWeb3(){
        const { ethereum } = window
        if(ethereum) 
            return ethereum
        else{
            setReportOnce(messageWeb3)
            a(messageWeb3)
            return;
        }
    }

    async function startLottery(){
        a("start lottery clicked")


        // @ts-ignore
        const ethereum = checkWeb3()
            try{
                // @ts-ignore
                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner();
                const contract = new ethers.Contract(contractAddress, abi, signer)

                //set time though
                const finalOutput_ = finalOutput;
                let currentTime = Math.floor(new Date().getTime() / 1000) 
                let timeLater = currentTime + (60*60*24)

                const startTime = currentTime //current time in seconds
                const endTime = timeLater;

                const nonce1_ = nonce1;

                const transaction = await contract.startLottery(finalOutput_,
                                                                  startTime,
                                                                  nonce1_,
                                                                  finalOutput,
                                                                  endTime)
                
                await transaction.wait()

                let showURL = "bscscan.com/tx/"+transaction.hash

                let forRandNumber = firsty.concat(secondy)
                let forDecipher = password

                let cipherForRandomNum = CryptoJS.AES.encrypt(forRandNumber, forDecipher).toString()
                let cipherForNonce2 = CryptoJS.AES.encrypt(nonce2, forDecipher).toString()

                insertData1(cipherForRandomNum, nonce1, cipherForNonce2, finalOutput)

                setReportOnce('TX Successful') //link to the transaction
                console.log('Transaction Hash: '+transaction.hash)
                
            }catch(err){
                        let error1 = JSON.stringify(err)
                        let error2 = JSON.stringify(err.reason)
                        
                        a(`Error5: ${error1}`)
                        setReportOnce(getStringOut(error2))
            }
    }

    function revealDraw1(){
        ab('reveal lottery clicked')

        let url2 = urlForRecord
        axios.post(url2,  {
                q: "2"
        }).then((response) => {
            let nonce2 = response.data.nonce2
            let randomNumber = response.data.random_num
            //let finalOutput = response.data.final_output

            let forDecipher = password// "Castle123"//password

            let bytes2  =  CryptoJS.AES.decrypt(nonce2, forDecipher)
            let decodedNonce2 = bytes2.toString(CryptoJS.enc.Utf8)
            a(`Nonce2 - ${decodedNonce2}`)

            let bytes3  =  CryptoJS.AES.decrypt(randomNumber, forDecipher)
            let randomy = bytes3.toString(CryptoJS.enc.Utf8)
            a(`Random Num - ${randomy}`)
            
            revealDraw2(decodedNonce2, randomy)


        }).catch(err => {
            a('error5: '+err)
            setReportOnce('error_data1'+err)
        })
    }

    async function revealDraw2(decodedNonce2, randomy){
        //ab('reveal lottery clicked')
        //function revealDraw(bytes32 _nonce2, string memory _luckyNumber) external

        // @ts-ignore
        const ethereum = checkWeb3()
            try{
                // @ts-ignore
                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner()
                const contract = new ethers.Contract(contractAddress, abi, signer)

                const transaction = await contract.revealDraw(decodedNonce2, randomy)
                
                await transaction.wait()

                let showURL = "bscscan.com/tx/"+transaction.hash

                setReportOnce('TX Successful') //link to the transaction
                console.log('Transaction Hash: '+transaction.hash)
                
            }catch(err){
                        let error1 = JSON.stringify(err)
                        let error2 = JSON.stringify(err.reason)
                        
                        a(`Error5: ${error1}`)
                        setReportOnce(getStringOut(error2))
            }
    }

    async function claimDraw(){
        // @ts-ignore
        const ethereum = checkWeb3()
            try{
                // @ts-ignore
                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner()
                const contract = new ethers.Contract(contractAddress, abi, signer)

                const transaction = await contract.claimTicketForDailyDrawAll()
                
                await transaction.wait()

                let showURL = "bscscan.com/tx/"+transaction.hash

                setReportOnce('TX Successful') //link to the transaction
                console.log('Transaction Hash: '+transaction.hash)
                
            }catch(err){
                        let error1 = JSON.stringify(err)
                        let error2 = JSON.stringify(err.reason)
                        //a(err)
                        
                        a(`Error5: ${error1}`)
                        let ck = getStringOut(error2)
                        if(ck == 'error98'){
                            setReportOnce(getStringOut2(err.data.message))
                        }else
                            setReportOnce(getStringOut(error2))
                        
            }
    }

    async function endLottery(){
        ab('end lottery clicked')

        // @ts-ignore
        const ethereum = checkWeb3()
            try{
                // @ts-ignore
                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner()
                const contract = new ethers.Contract(contractAddress, abi, signer)

                const transaction = await contract.endLottery()
                
                await transaction.wait()

                let showURL = "bscscan.com/tx/"+transaction.hash

                setReportOnce('TX Successful') //link to the transaction
                console.log('Transaction Hash: '+transaction.hash)
                
            }catch(err){
                        let error1 = JSON.stringify(err)
                        let error2 = JSON.stringify(err.reason)
                        
                        a(`Error5: ${error1}`)
                        setReportOnce(getStringOut(error2))
            }
    }

    const modalRef = useRef()

    const [modal, setModal] = useState(false)
    const [report, setReport] = useState("")

    const messageWeb3 = "Use Browser that supports Web3 and Account Connected"

    function hideModal(){
        setModal(false)
    }

    function setReportOnce(data){
        setModal(true)
        setReport(data)
    }

    function a(v){console.log(v)}

    function changy(event){
        if(document.visibilityState == "visible"){
            //a('tab is active')
            loaded()
        }
        //else
            //a('tab is not active')
    }

    useEffect(() => {

        function handler3(event){
            if(!modalRef.current) return 
            // @ts-ignore
            if(!modalRef.current.contains(event.target)){
                setModal(false)
            }
        }

        loaded()

        gg()

        document.addEventListener('click', handler3, true)
        document.addEventListener('visibilitychange', changy, true)

        return () => {
            document.removeEventListener('click', handler3)
            document.removeEventListener('visibilitychange', changy)
        }
    }, [])

    function showModal(){
        return (
            <div style={{display: 'block'}} className='modal'>
            <div ref={modalRef} className='modal-content w3-animate-zoom'>
                <span onClick={hideModal} className='close'>&times;</span>
                <h5 style={{color: "#000"}}>Report:</h5>
                <h4 style={{color: "#000", fontWeight: 'bolder'}}>{report}</h4>
                
            </div>
        </div>
        )
    }

    function handleChange1(event){
        setClose(false)
        let k = event.target.value
        if(k>=0 && k<=9 && k.length <= 1){
            a('valu: '+k)
            setFirsty(k)
            if(secondy !== "" && k !== ""){
                let m = k.concat(secondy)
                randy(m)
                //a(m)
            }
        }else{
            let m = "Input must be between 0 and 9 (inclusive) and Whole Number"
            a(m)
            setClose(true)
        }
    }

    function handleChange2(event){
        setClose(false)
        let k = event.target.value
        if(k>=0 && k<=9 && k.length <= 1){
            a('valu: '+k)
            setSecondy(k)
            if(firsty !== "" && k !== ""){
                let m = firsty.concat(k)
                randy(m)
                //a(m)
            }
        }else{
            let m = "Input must be between 0 and 9 (inclusive) and Whole Number"
            a(m)
            setClose(true)
        }
    }

    const [firsty, setFirsty] = useState("")
    const [secondy, setSecondy] = useState("")
    const [close, setClose] = useState(false)
    const [nonce1, setNonce1] = useState("")
    const [nonce2, setNonce2] = useState("")
    const [finalOutput, setFinalOutput] = useState("")
    const [password, setPassword] = useState("")

    const error = "Number must be between 0 and 9 (inclusive)"

    function randy(_input){
        //a('rand button clicked')
        let output = ""
        if(_input == "xx") 
            output = cc()
        else 
            output = _input
        let first = output.charAt(0)
        let second = output.charAt(1)

        setFirsty(first)
        setSecondy(second)

        let {nonce1, nonce2, finalOutput} = pp(output)

        setNonce1(nonce1)
        setNonce2(nonce2)
        setFinalOutput(finalOutput)

        //let forRandNumber = output
        //let forDecipher = password// nonce1.concat(nonce2).concat(finalOutput).concat(password) //i used this to mix/encrypt

        //let cipherForRandomNum = CryptoJS.AES.encrypt(forRandNumber, forDecipher).toString()
        //let cipherForNonce2 = CryptoJS.AES.encrypt(nonce2, forDecipher).toString()

        //a(cipherForRandomNum)
        //a(cipherForNonce2)

        /*let bytes  =  CryptoJS.AES.decrypt(cipherForRandomNum, forDecipher)
        let decodedRandNum =  bytes.toString(CryptoJS.enc.Utf8)
        a(`${password} - ${decodedRandNum}`)

        let bytes2  =  CryptoJS.AES.decrypt(cipherForNonce2, forDecipher)
        let decodedNonce2 = bytes2.toString(CryptoJS.enc.Utf8)
        a(`${password} - ${decodedNonce2}`)*/
        
        //insertData1(cipherForRandomNum, nonce1, cipherForNonce2, finalOutput)
        //getData1()
    }

    function insertData1(cipherForRandomNum, nonce1, cipherForNonce2, finalOutput){
        let url2 = urlForRecord
        axios.post(url2,  {
                random_num: cipherForRandomNum,
                nonce1: nonce1,
                nonce2: cipherForNonce2,
                final_output: finalOutput,
                q: "1"
        }).then((response) => {
            let kk = response.data// JSON.stringify(response.data)
            a('good: '+ kk)
        }).catch(err => {
            a('error5: '+err)
        })
    }

    function forPassword(event){
        let k = event.target.value
        a(k)
        localStorage.setItem("busd_lottery_p", k)
        setPassword(k)
    }

    function gg(){
        let m = localStorage.getItem("busd_lottery_p")
        if(m !== "") setPassword(m)
    }

    function ggg(){
        return (
            
            <div className="w3-card-4 w3-round-xlarge w3-small w3-center" 
                                    style={{marginBottom: '20px', paddingBottom: '8px'}}>
                
                {modal && showModal()}
                <div id="adminID" style={{marginBottom: '5px'}} className="w3-teal w3-padding-small w3-center w3-large">Admin Panel</div>

                {close && <div className='w3-row w3-center'>
                        <span className='w3-padding-small w3-small w3-red w3-round-xlarge'>{error}</span></div>}

                <div style={{margin: '4px'}} className="w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge">
                    <div><input onChange={handleChange1} value={firsty} type="number" className="w3-round-xlarge w3-large w3-center" 
                                    style={{width: '13%', margin: '1px', }}/>
                    <input onChange={handleChange2} value={secondy} type="number" className="w3-round-xlarge w3-large w3-center" 
                                    style={{width: '13%', margin: '2px', fontSize: '12px'}}/>
                                    </div>
                    Nonce1:<input disabled={true} placeholder='...nonce1' value={nonce1} className="w3-round-xlarge w3-large w3-center" 
                                    style={{margin: '2px',}}/>
                    <br/>Nonce2:<input disabled={true} placeholder='...nonce2' value={nonce2} className="w3-round-xlarge w3-large w3-center" 
                                    style={{margin: '2px',}}/>
                    <br/>FinalOutput:<input disabled={true} placeholder='...final output' value={finalOutput} className="w3-round-xlarge w3-large w3-center" 
                                    style={{margin: '2px',}}/>
                    <br/>Password:<input placeholder='...password to encrypt data' onChange={forPassword} value={password} className="w3-round-xlarge w3-large w3-center" 
                                    style={{margin: '2px',}}/>
                    <button style={{margin: '2px', fontSize: '13px'}} className="w3-button w3-round-xlarge 
                                        w3-padding-small w3-teal menuHover" 
                                        onClick={startLottery}><b><span style={{padding: '2px', margin: '2px'}} className='w3-red w3-round-xxlarge'>1.</span>Start Lottery</b></button>                                                                    
                    <button style={{marginLeft: '10px'}} className="w3-button w3-round-xlarge 
                                        w3-padding-small w3-teal menuHover" onClick={() => randy("xx")}>Randomize</button>

                </div>  

                <div style={{margin: '4px', marginTop: '20px'}} className="w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge">
                    <button style={{margin: '2px', fontSize: '13px'}} className="w3-button w3-round-xlarge w3-padding-small 
                                        w3-teal menuHover" onClick={endLottery}><span style={{padding: '2px', margin: '2px'}} 
                                        className='w3-red w3-round-xxlarge'>2.</span><b>End Lottery</b></button>
                </div>

                <div style={{margin: '4px', marginTop: '20px'}} className="w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge">
                Password:<input placeholder='...password to encrypt data' onChange={forPassword} value={password} className="w3-round-xlarge w3-large w3-center" 
                                    style={{margin: '2px',}}/>
                    <button style={{margin: '2px', fontSize: '13px'}} className="w3-button w3-round-xlarge w3-padding-small 
                                        w3-teal menuHover" onClick={revealDraw1}><span style={{padding: '2px', margin: '2px'}} 
                                        className='w3-red w3-round-xxlarge'>3.</span><b>Reveal</b></button>
                </div>

                <div style={{margin: '4px', marginTop: '20px'}} className="w3-row w3-center w3-padding-small w3-border w3-small w3-round-xlarge">
                    <button style={{margin: '2px', fontSize: '13px'}} className="w3-button w3-round-xlarge w3-padding-small 
                                        w3-teal menuHover" onClick={claimDraw}><span style={{padding: '2px', margin: '2px'}} 
                                        className='w3-red w3-round-xxlarge'>4.</span><b>Claim</b></button>
                </div>
            </div>
        )
    }

    return (
        <>
            {admin && ggg()}
        </>
        
    )
    
}

const Harry = () => {

    return(
        <>
            <div className='w3-center' style={{marginTop: '5px'}}>
                <ChoiceSelect />
            </div>        
        </>
    )
}

function ab(v){console.log(v)}  

export {Harry, AdminPanel}