function getStringOut(data){
    if(typeof(data) != "undefined"){
        let newString = data.substring(data.indexOf("'")+1, data.lastIndexOf("'"))
        if(newString.length>0)
            return newString
        return data
    }else{//data variable is undefined
        return "error98"
    }
    
}

function getStringOut2(data){
    if(typeof(data) != "undefined"){
        let newString = data.substring(data.indexOf("\"")+1, data.lastIndexOf("\""))
        if(newString.length>0)
            return newString
        return data
    }else{//data variable is undefined
        return "error98"
    }
    
}

