import { ethers } from "ethers";
import { useEffect, useRef, useState } from "react";
import { abi, bnb_main, busdAddress, contractAddress, getStringOut } from "../first/Var";
import { return2Rand } from "./randomh";

function ChoiceSelect() {

    let options = new Array(200);

    const [choice, setChoice] = useState(1)
    const [first, setFirst] = useState([''])
    const [second, setSecond] = useState([''])
    const [close, setClose] = useState(false)
    const [timeToLapse, setTimeLapse] = useState("...")
    const [lotteryID, setLotteryID] = useState("...")
    const [lotteryStatus, setLotteryStatus] =  useState("...")

    for(let i=0; i<200; i++){
        options[i] = i+1
    }

    const selectOptiony = (event) => {
        let hh = event.target.value
        //a('selected is: '+hh)
        setChoice(hh)
        utiLg(hh)
    }

    function lotteryEndsIn(endTime){
        return timeSince(endTime)
    }

    const timeSince = (date) => {

        // @ts-ignore
        var seconds = Math.floor((date*1000 - new Date()) / 1000);
      
        var interval = seconds / 31536000;
      
        if (interval > 1) {
          return Math.floor(interval) + " years";
        }
        interval = seconds / 2592000;
        if (interval > 1) {
          return Math.floor(interval) + " months";
        }
        interval = seconds / 86400;
        if (interval > 1) {
          return Math.floor(interval) + " days";
        }
        interval = seconds / 3600;
        if (interval > 1) {
          return Math.floor(interval) + " hours";
        }
        interval = seconds / 60;
        if (interval > 1) {
          return Math.floor(interval) + " minutes";
        }
        return Math.floor(seconds) + " seconds";
      }

    async function getData(){
            a('Getting Initial data OnChain...')
            const ethereum = checkWeb3()
                try{
                    const provider = new ethers.providers.Web3Provider(ethereum)
                    const signer = provider.getSigner()
                    const contract = new ethers.Contract(contractAddress, abi, signer)
    
                    const datas = await contract.getKeyDatas()
                    a(`Data are2: ${datas}`)
                    let endTime = parseInt(datas[5]._hex, 16)
                    let cc = lotteryEndsIn(endTime)
                    setTimeLapse(cc)
                    let lotteryID = parseInt(datas[3]._hex, 16)
                    setLotteryID(lotteryID)
                    let uu = parseInt(datas[11]._hex, 16)
                    if(uu == 1) setLotteryStatus("In Play")
                    else setLotteryStatus("Completed")
                    let um = parseInt(datas[12]._hex, 16)
                    setTotalBet(um)
                    setSlider(false)
                    
                }catch(err){
                            let error1 = JSON.stringify(err)
                            let error2 = JSON.stringify(err.reason)
                            setSlider(false)
                            
                            a(`Error1: ${error1}`)
                            setReportOnce(getStringOut(error2))
                }
    }

    useEffect(() => {

        getData()

        function handler3(event){
            if(!modalRef.current) return 
            // @ts-ignore
            if(!modalRef.current.contains(event.target)){
                setModal(false)
            }
        }

        function changy(event){
            if(document.visibilityState == "visible"){
                a('tab is active')
                //refreshData()
            }
            else
                a('tab is not active')
        }

        document.addEventListener('click', handler3, true)
        //document.addEventListener('visibilitychange', changy, true)

        return () => {
            document.removeEventListener('click', handler3)
            //document.removeEventListener('visibilitychange', changy)
        }
    }, [])

    const handleChange1 = (event, indy) => {
        setClose(false)
        let k = event.target.value
        if(k>=0 && k<=9 && k.length <= 1){
            a('valu: '+k)
            let m = [...first]
            m[indy] = k
            setFirst(m)
        }else{
            let m = "Input must be between 0 and 9 (inclusive) and Whole Number"
            a(m)
            setClose(true)
        }
    }

    const error = "Number must be between 0 and 9 (inclusive)"

    const handleChange2 = (event, indy) => {
        setClose(false)
        let k = event.target.value
        if(k>=0 && k<=9 && k.length <= 1){
            a('valu: '+k)
            let m = [...second]
            m[indy] = k
            setSecond(m)
        }else{
            let m = "input must be between 0 and 9"
            a(m)
            setClose(true)
        }
    }

    function clearAll(){
            //setFirst(new Array(choice).fill(''))
            utiLg(choice)
    }

    function utiLg(size){
        let m, n;

        m = new Array(size)
        n = new Array(size)

        for(let a=0; a<size; a++){
            m[a] = ""
            n[a] = ""
        }
        setFirst(m)
        setSecond(n)
    }

    function randy(){
        //setTimeout(randy2, 500)
        randy2()
    }

    function randy2(){
        let m, n //, first_, second_

        m = [...first]
        n = [...second]
        
        /*for(let i=0; i<choice; i++){
            
            first_ = Math.floor(Math.random() * 10)
            second_ = Math.floor(Math.random() * 10)

            m[i] = first_.toString()
            n[i] = second_.toString()
        }*/
        for(let i=0; i<choice; i++){
            let rand = return2Rand()
            m[i] = rand[0]
            n[i] = rand[1]
        }
        setFirst(m)
        setSecond(n)
    }

    const [modal, setModal] = useState(false)
    const [report, setReport] = useState("")

    function setReportOnce(data){
        setModal(true)
        setReport(data)
    }

    const modalRef = useRef()

    function hideModal(){
        setModal(false)
    }

    function showModal(){
        return (
            <div style={{display: 'block'}} className='modal'>
            <div ref={modalRef} className='modal-content w3-animate-zoom'>
                <span onClick={hideModal} className='close'>&times;</span>
                <h5 style={{color: "#000"}}>Report:</h5>
                <h4 style={{color: "#000", fontWeight: 'bolder'}}>{report}</h4>
                
            </div>
        </div>
        )
    }   

    const messageWeb3 = "Use Browser that supports Web3 and Account Connected"

    async function playData(){
        const ethereum = checkWeb3()
            try{
                let mm = Array(choice)
                for(let i=0; i<choice; i++){
                    a(`First: ${first[i]} and Second: ${second[i]}`)
                    mm[i] = first[i].concat(second[i])
                    a(mm[i])
                }

                const spender_ =  contractAddress

                const provider = new ethers.providers.Web3Provider(ethereum)
                const signer = provider.getSigner();

                let contract2 = new ethers.Contract(contractAddress, abi, signer)

                const kk = await contract2.getKeyDatas()
                let busdPerBet = parseInt(kk[9]._hex, 16)
                a('busd per bet: '+busdPerBet)

                let amountToWithdraw = (busdPerBet * mm.length * Math.pow(10, 18)).toString()

                //const tx1 = await contract.
                if(kk[0]){//user yet to register
                    if(kk[11] == 1){//no ongoing lottery draw
                        const contract = new ethers.Contract(busdAddress, abi, signer)
                        const transaction = await contract.approve(spender_, amountToWithdraw)
                        
                        await transaction.wait()

                        let transaction2 = await contract2.buyTickets(mm)
                        await transaction2.wait()

                        console.log('Transaction Hash: '+transaction.hash)
                        refreshData()
                        setSlider(false)
                        setReportOnce('Tx Successful')
                        
                    }else{
                        a(`Error41: No Ongoing Lottery Game`)
                        refreshData()
                        setSlider(false)
                        setReportOnce("No ongoing Lottery Game")
                        return
                    }
                
                }else{
                    a(`Error40: User yet to Register`)
                    refreshData()
                    setSlider(false)
                    setReportOnce("User yet to Register")
                    return
                }
                
                
            }catch(err){
                        let error1 = JSON.stringify(err)
                        let error2 = JSON.stringify(err.reason)
                        
                        a(`Error4: ${error1}`)
                        refreshData()
                        setSlider(false)
                        setReportOnce(getStringOut(error2))
            }
        
    }    

    function checkWeb3(){
        setSlider(true)
        const { ethereum } = window
        if(ethereum) 
            return ethereum
        else{
            setReportOnce(messageWeb3)
            a(messageWeb3)
            return;
        }
    }

    function neutral(){
        setClose(false)
    }

    function refreshData(){
        getData()
    }

    const [slider, setSlider] = useState(false)
    const sliderImg = <img style={{}} className='w3-spin' src='graphics/icons8-loader-30.png' width={25} height={25} />
    const [totalBetForThis, setTotalBet] = useState('')

    return (
        <div>

            {modal && showModal()}
            
            <div style={{margin: '2px', fontSize: '12px'}}>
                <div><span className="w3-round-xlarge w3-padding-small" 
                            style={{marginLeft: '10px', fontSize: '13px'}}>Lottery Ends in: <span style={{fontSize: '17px'}} className="w3-red w3-round-xlarge w3-padding-small">{timeToLapse}</span></span></div>
                <div style={{marginTop: '5px'}}><span className="w3-round-xlarge w3-padding-small" 
                            style={{marginLeft: '10px', fontSize: '13px'}}>Current Lottery: <span style={{fontSize: '17px'}} className="w3-red w3-round-xlarge w3-padding-small">{lotteryID}</span></span></div>
                <div style={{marginTop: '5px'}}><span className="w3-round-xlarge w3-padding-small" 
                            style={{marginLeft: '10px', fontSize: '13px'}}>Lottery Playing: <span style={{fontSize: '17px'}} className="w3-red w3-round-xlarge w3-padding-small">{lotteryStatus}</span></span></div>
                <div style={{marginTop: '5px'}}><span className="w3-round-xlarge w3-padding-small" 
                            style={{marginLeft: '10px', fontSize: '13px'}}>Total Draw: <span style={{fontSize: '17px'}} className="w3-red w3-round-xlarge w3-padding-small">{totalBetForThis}</span></span></div>
                <div style={{marginTop: '5px', fontSize: '14px'}}><a href={`${bnb_main}address/${contractAddress}`}>Check Lottery Details on BscScan</a></div>
                
                <div style={{marginBottom: '5px'}}></div>
                <a href="#busd_lottery"><img id="circleImg" src="images/giphy2.gif" alt="busd lottery logo" /></a>
            </div>
                <select onChange={selectOptiony} style={{width: "14%", marginTop: '1px', marginBottom: '5px',}} 
                                    className="w3-medium w3-select w3-round-large w3-medium w3-center" name="option">
                                {options.map((option, index) => {
                                    return <option key={index}>{option}</option>
                                })}
                    </select>

                    {close && <div className='w3-row w3-center'>
                        <span className='w3-padding-small w3-small w3-red w3-round-xlarge'>{error}</span></div>}
            
            {
            first.map((vv, indy) => {
                    return <div key={'fs_'+indy}>
                                <input onBlur={neutral} value={first[indy]} onChange={(e) => handleChange1(e, indy)} type="number" className="w3-round-xlarge w3-large w3-center" 
                                    style={{width: '13%', margin: '2px', fontWeight: 'bold'}}/>
                                <input onBlur={neutral} value={second[indy]} onChange={(e) => handleChange2(e, indy)} type="number" className="w3-round-xlarge w3-large w3-center" 
                                    style={{width: '13%', margin: '2px', fontWeight: 'bold'}}/>
                                    
                                
                            </div>
                })
            }

            <div className="w3-center"><button style={{margin: '2px', fontSize: '13px'}} className="w3-button w3-round-xlarge w3-padding-small w3-teal menuHover" onClick={playData}><b>Play</b></button>
            {slider && sliderImg}
            </div>
            <div className="w3-center"><button style={{margin: '2px', fontSize: '10px', marginTop: "20px"}} className="w3-button w3-round-xlarge w3-padding-small w3-teal menuHover" onClick={refreshData}>Refresh</button>
            {slider && sliderImg}
            </div>
        </div>
    )
}

function a(v){
    console.log(v)
}

export {ChoiceSelect}